}

.search-form {
  background-color: #5db5a4;
  justify-content: center;
  gap: 10px;
  margin-bottom: 20px;
  padding-top: 15px;
  padding-bottom: 15px;
  display: flex;
}

input {
  border-color: #2a695e;
  border-radius: 4px;
